.book {
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
	background-color: #FFF;
	transition: opacity 2s, box-shadow 2s, margin 2s, width 2s, height 2s, -webkit-transform 2s, -webkit-filter 2s;
	transition: transform 2s, opacity 2s, filter 2s, box-shadow 2s, margin 2s, width 2s, height 2s;
	transition: transform 2s, opacity 2s, filter 2s, box-shadow 2s, margin 2s, width 2s, height 2s, -webkit-transform 2s, -webkit-filter 2s;
  /* margin-left: 15%; */
  /* margin-top: 15%; */
}

.book.hover {
	box-shadow: 0px 7px 20px #0005;
	opacity: 1;
	-webkit-filter: none;
	        filter: none;
}

.sticker {
	background-color: #FFF;
	box-shadow: 0 6px 16px -6px #000;
  /* border-radius: 20px; */
	display: inline-block;
	/* margin: .5vmin; */
  /* border-radius: 2vmin; */
	overflow: hidden;
	position: relative;
	background-size: 100%;
	background-position: center;
	transition: top .5s, left .5s, width .5s, height .5s, opacity .5s, -webkit-transform 1s;
	transition: transform 1s, top .5s, left .5s, width .5s, height .5s, opacity .5s;
	transition: transform 1s, top .5s, left .5s, width .5s, height .5s, opacity .5s, -webkit-transform 1s;
	z-index: 2;
	background-repeat: no-repeat;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  opacity: 0.5;
}

.sticker.valid {
  -webkit-filter: none;
          filter: none;
  opacity: 1;
}

.book .sticker:hover {
	-webkit-transform: scale(1.05) rotate(1deg);
	        transform: scale(1.05) rotate(1deg);
	z-index: 3;
  opacity:1;
}
.book .sticker:nth-child(odd):hover {
	-webkit-transform: scale(1.05) rotate(-1deg);
	        transform: scale(1.05) rotate(-1deg);
	z-index: 3;
}

.sticker .note {
	border: 3px solid #000;
	background-color: #FFF;
	-webkit-transform: skewX(15deg);
	        transform: skewX(15deg);
	position: absolute;
	padding: 3px 10px;
	font-family: 'Komika Display', cursive;
}

.sticker .note.bottom-left {
	left: -7px;
	bottom: -3px;
}

.sticker .note.top-right {
	right: -7px;
	top: -3px;
}

.sticker .note.bottom-left {
  bottom: -3px;
  left: -7px;
}

.book .bb-item {
	display: flex;
	justify-content: center;
}

.book .content.moving {
	-webkit-transform-origin: left;
	        transform-origin: left;
	-webkit-transform: rotateY(-178deg);
	        transform: rotateY(-178deg);
}

.book .content {
	float: left;
	display: grid;
	grid-template-columns: auto auto;
	grid-template-rows: auto auto auto;
	width: 50%;
	height: 100%;
	box-shadow: 10px 0 20px -10px #0007 inset;
	background-color: #FFF;
}

.book .content:nth-child(2n+1) {
	box-shadow: -10px 0 20px -10px #0007 inset;
}

.book .content.left {
	-webkit-transform-origin: right;
	        transform-origin: right;
	-webkit-transform: rotateY(15deg);
	        transform: rotateY(15deg);
}

.book .content.right {
	-webkit-transform-origin: left;
	        transform-origin: left;
	-webkit-transform: rotateY(-15deg);
	        transform: rotateY(-15deg);
}

