.documents .content p {
    width: 100%;
    color: #CCC;
    text-align: left;
    font-family: 'Goldie Boxing', sans-serif;
    font-size: 4vmin;
    padding: 1vmin 15vmin;
    -webkit-transform: scale(3);
            transform: scale(3);
    -webkit-filter: blur(1vmin);
            filter: blur(1vmin);
    opacity: 0;
    transition: opacity 1s, -webkit-transform 1s, -webkit-filter 1s;
    transition: transform 1s, filter 1s, opacity 1s;
    transition: transform 1s, filter 1s, opacity 1s, -webkit-transform 1s, -webkit-filter 1s;
}

.documents.show .content p {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-filter: blur(0);
            filter: blur(0);
    opacity: 1;
}

.landing .title {
    font-size: calc(8vmin + 8vmax);
    width: 100%;
    text-align: center;
    margin: 5vmin auto 5vmin auto;
    font-family: 'Indiana-Jonas', sans-serif;
    color: #FFF;
    position: relative;
}

.images > div {
    margin: auto -8vmin;
    display: inline-block;
    border-radius: 20px;
    transition: background-size 2s, margin 2s, -webkit-transform 2s;
    transition: background-size 2s, transform 2s, margin 2s;
    transition: background-size 2s, transform 2s, margin 2s, -webkit-transform 2s;
    position: relative;
}

.images > div.active {
    margin: auto -5vmin;
}

.images > div > div {
    width: 25vmin;
    height: 35vmin;
    background-size: contain;
    background-position: bottom;
    background-repeat: no-repeat;
    border-radius: 10px;
}

.images {
    margin-top: 7vmin;
    position: relative
}

.images::before {
    content: '';
    position: absolute;
    background-color: #0005; 
    border-radius: 100%;
    width: 50%;
    left: 25%;
    bottom: 0;
    height: 10%;
}

.btn {
    font-size: 20px;
    height: 50px;
    width: 150px;
}

.line {
    background-color: #FFF7;
    margin: 0 auto;
    height: .1vmin;
    width: 100%;
}

.landing .content {
    width: 100%;
    margin: 2vmin auto;
    text-align: center;
}

.popup {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 400px;
    background-color: #FFF;
    border-radius: 5vmin;
    padding: 32px;
}

@-webkit-keyframes floatingText {
    0% {
        -webkit-transform: scale(1.02) rotate(2deg);
                transform: scale(1.02) rotate(2deg);
    }
    30% {
        -webkit-transform: scale(0.98) rotate(-2deg);
                transform: scale(0.98) rotate(-2deg);
    }
    60% {
        -webkit-transform: scale(1) rotate(2deg);
                transform: scale(1) rotate(2deg);
    }
    100% {
        -webkit-transform: scale(1.02) rotate(2deg);
                transform: scale(1.02) rotate(2deg);
    }
}

@keyframes floatingText {
    0% {
        -webkit-transform: scale(1.02) rotate(2deg);
                transform: scale(1.02) rotate(2deg);
    }
    30% {
        -webkit-transform: scale(0.98) rotate(-2deg);
                transform: scale(0.98) rotate(-2deg);
    }
    60% {
        -webkit-transform: scale(1) rotate(2deg);
                transform: scale(1) rotate(2deg);
    }
    100% {
        -webkit-transform: scale(1.02) rotate(2deg);
                transform: scale(1.02) rotate(2deg);
    }
}
